import {registerBlockType} from '@wordpress/blocks';
import './style.scss';
import metadata from './block.json';
import {InspectorControls, RichText, useBlockProps} from "@wordpress/block-editor";
import {
    ColorPalette,
    PanelBody,
    PanelRow,
    TextControl,
    RadioControl,
} from "@wordpress/components";


registerBlockType(metadata.name, {
    edit: (props) => {
        return (
            <div {...useBlockProps()}>
                <RichText tagName="p" className={`p--text ${props.attributes.position === 'center' ? 'p--center' : ''}`}
                          style={{backgroundColor: props.attributes.color}}
                          value={props.attributes.text} onChange={(value) => props.setAttributes({text: value})}>

                </RichText>
                <InspectorControls key="setting">
                    <PanelBody title={'区块设置'} icon={"more"} initialOpen={true}>
                        <PanelRow>
                            <div id="gutenpride-controls">
                                <fieldset>
                                    <PanelRow>颜色</PanelRow>
                                    <ColorPalette
                                        onChange={(value) => props.setAttributes({color: (value)})}
                                        colors={[
                                            {name: 'primary', color: '#007bff'},
                                            {name: 'success', color: '#67C23A'},
                                            {name: 'warning', color: '#E6A23C'},
                                            {name: 'danger', color: '#F56C6C'},
                                            {name: 'info', color: '#909399'},
                                            {name: 'black', color: '#303133'},
                                        ]}
                                        value={props.attributes.color}
                                    />
                                </fieldset>
                                <fieldset>
                                    <PanelRow>图标</PanelRow>
                                    <TextControl value={props.attributes.iconClass}
                                                 onChange={(value) => props.setAttributes({iconClass: (value)})}>

                                    </TextControl>
                                </fieldset>
                                <fieldset>
                                    <PanelRow>位置</PanelRow>
                                    <RadioControl selected={props.attributes.position}
                                                  onChange={(value) => props.setAttributes({position: (value)})}
                                                  options={[
                                                      {label: '靠左', value: 'left'},
                                                      {label: '居中', value: 'center'},
                                                  ]}>
                                    </RadioControl>
                                </fieldset>
                            </div>
                        </PanelRow>
                    </PanelBody>
                </InspectorControls>
            </div>
        )
    },
    save: (props) => {
        return (
            <div {...useBlockProps.save()}>
                <p class={`p--text ${props.attributes.position === 'center' ? 'p--center' : ''}`}
                   style={{backgroundColor: props.attributes.color}}>
                    {props.attributes.text}
                </p>
            </div>
        );
    },
});
